.movie-container {
    border: 1px solid  #e2d7d7;
    box-shadow: 12px 12px 2px 1px rgba(98, 98, 132, 0.2);
    text-align: center;
    background-color: #efefef;
    color: rgb(39, 24, 24);
    width: 300px;
    height: 480px;
    min-height: 200px;
    margin: 20px;
    padding: 10px;
    justify-content: space-between;
    font-family: 'Dosis', sans-serif;
}
.bold {
    font-weight: bold;
}

.btn-edit {
    /* width: 50px; */
    /* align-self: center; */
    /* margin-top: 10px; */
    /* background-color: white;
    padding: 5px;
    border-radius: 7px; */
    /* margin: 5px auto; */
}

.a-title {
    /* text-transform: lowercase; */
    height: 55px;
    display: block;
}

.title {
    /* display: block; */
    /* text-transform: lowercase; */
    color: rgb(135, 127, 152);
    /* text-shadow: 1px 1px 1px black; */
    text-transform: capitalize;
    /* z-index: 5; */
}

.preview-body {
    justify-items: center;
}
.movie-container>img {
    width: 180px;
    height: 275px;
    margin: auto;
    /* z-index: 3; */
}

.year-container {
    margin: 0 auto;
}
.genre-list {
    margin: auto;
    padding: 0;
}
.genre {
    border: 1px solid #dcc3c3;
    list-style-type: none;
    padding: 5px;
    margin: 0 5px;
    border-radius: 8px;
    border-radius: 4px;
    min-width: 80px;
}

.user-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: 50px;
}

.movie-list {
    text-decoration: none;
    list-style-type: none;
}

.movies-list {
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 0;
}

.sub-title {
    margin: 0 auto;
    
    /* align-items: center; */
}

.sub-title>p {
    font-size: 2em;
    /* margin-right: 20px; */
    margin-top: 10px;
    font-family: 'Dosis', sans-serif;
    /* z-index: 5; */
}

.add-btn {
    margin-left: 20px;
    /* font-family: 'Dosis', sans-serif; */
    /* color: white; */
    /* border-radius: 11px; */
    font-size: 1.2em;
    /* background-color: rgb(161, 148, 188); */
    /* cursor: pointer; */
}
.add-btn>span {
    margin-right: 5px;
    font-weight: bold;
}
.movie-preview {
    margin: auto;
}

@media (max-width: 520px) {
    .sub-title {
        flex-direction: column;
        align-items: center;
    }
}

.edit {
    margin: auto;
    /* width: 75%; */
}

.form-edit {
    width: 50%;
    margin: auto;
    border: 1px solid;
    padding: 10px;
}
.input-edit {
    margin: 10px;
    height: 30px;
    width: 350px;
    padding: 5px;
    justify-items: flex-start;
}

.edit-title {
    text-align: center;
}

.input-container {
    align-items: center;
    justify-content: flex-end; 
    margin-right: 40px;
}

.input-container>p {
    font-weight: bold;
    margin-right: 20px;
}

.btns {
    margin: 0 auto;
    justify-content: space-around;
}
.btns-edit {
    width: 50%;
}

.btn {
    align-self: center;
    margin-top: 10px;
    background-color: white;
    padding: 10px;
    border-radius: 7px;
    font-size: 1.1em;
    width: 80px;
}

.btn:hover {
    opacity: 0.5;
}

@media (max-width: 980px) {
    .form-edit {
        width: 60%;
    } 
}

@media (max-width: 820px) {
    .form-edit {
        width: 70%;
    } 
}

@media (max-width: 720px) {
    .form-edit {
        width: 85%;
    } 
}

@media (max-width: 600px) {
    .input-container {
        flex-direction: column;
    } 
}
.movie-details {
    border: 1px solid #e2d7d7;
    box-shadow: 12px 12px 2px 1px rgba(98, 98, 132, 0.2);
    text-align: center;
    background-color: #efefef;
    color: rgb(39, 24, 24);
    width: 500px;
    height: 350px;
    min-height: 200px;
    margin: 20px auto;
    padding: 10px;
    justify-content: space-around;
    font-family: 'Ubuntu', sans-serif;
}

.details-container {
    justify-content: space-around;
}
.details-container>img {
    width: 180px;
    height: 275px;
}

.title-details {
    /* text-transform: capitalize; */
    /* height: 45px; */
    /* color: rgb(135, 127, 152); */
    /* text-shadow: 1px 1px 1px black; */
    margin: auto;
    font-size: 3em;
}

.movie-text {
    justify-content: space-between;
}
.director, .actors {
    margin-bottom: 15px;
}
.btn-delete, .btn-back {
    margin-left: 15px;
}

.plot {
    margin: 10px;
}

@media (max-width: 600px) {
    .details-container {
        flex-direction: column;
    }
    .details-container>img {
        width: 200px;
        height: 250px;
        margin: auto;
    }
    .movie-details {
        height: 450px;

    }
}
